.SignOutBtn {
	position: absolute;
	top: 0;
	right: 0;
	margin-top: 10px;
	margin-right: 10px;
	cursor: pointer;
	padding: 10px 15px;
	background-color: transparent;
	color: white;
	border: none;
	font-size: 14px;
	border: 1px solid white;
	border-radius: 25px;
	text-transform: uppercase;
}

.dashTitle {
	width: 100%;
	text-align: center;
}

.userEmail {
	color: red;
}
.instructions {
	width: 100%;
	text-align: center;
}

@media (max-height: 734px) {
	.SignOutBtn {
		font-size: 9px;
	}
}
