@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
html,
body,
#root {
	margin: 0;
	padding: 0;
	background-color: black;
	color: white;
	font-family: 'Bebas Neue';
}

.section {
	display: flex;
	width: 80%;
	margin: auto;
	margin-top: 2%;
}

.landingImg {
	width: 100%;
	height: auto;
	margin: 0;
	border-radius: 50px 0 0 50px;
	border-right: none;
}

.pBox {
	max-height: 400px;
	border: solid 1px white;
	border-radius: 0 50px 50px 0;
	border-left: none;
	display: flex;
}

.section p {
	max-width: 60%;
	float: left;
	margin-bottom: auto;
	margin-top: auto;
	margin-left: 15px;
	font-size: 24px;
	line-height: 2.1rem;
	letter-spacing: 1px;
}
.section p:first-child::first-letter {
	float: left;
	font-size: 75px;
	line-height: 60px;
	padding-top: 4px;
	padding-right: 8px;
	padding-left: 3px;
}

.btnRow {
	display: flex;
	justify-content: right;
	margin: auto 15%;
	width: auto;
	margin-top: 30px;
}

.btnRow button {
	margin: 0 5px;
	cursor: pointer;
	padding: 10px 15px;
	background-color: transparent;
	color: white;
	border: none;
	font-size: 24px;
	border: 1px solid white;
	border-radius: 50px;
	text-transform: uppercase;
}

@media (max-width: 960px) {
	.section p {
		line-height: 1.3rem;
		font-size: 16px;
	}
	.section p:first-child::first-letter {
		float: left;
		font-size: 40px;
		line-height: 60px;
		padding-top: 4px;
		padding-right: 8px;
		padding-left: 3px;
	}
}

@media (max-width: 720px) {
	.section p {
		line-height: 1.3rem;
		font-size: 14px;
		max-width: 70%;
	}
	.section p:first-child::first-letter {
		float: left;
		font-size: 40px;
		line-height: 60px;
		padding-top: 4px;
		padding-right: 8px;
		padding-left: 3px;
	}
}

@media (max-width: 650px) {
	.landingImg {
		display: none;
	}
	.pBox {
		max-height: 400px;
		border: none;
		border-radius: 50px 50px;
		border-left: none;
		display: flex;
	}
	.section p {
		margin: auto;
		max-width: 100%;
		margin-left: 5px;
		margin-top: 15px;
	}
	.btnRow {
		margin: auto;
		margin-right: 20px;
		width: auto;
		margin-top: 30px;
	}

	.btnRow button {
		font-size: 10px;
	}
}

.title {
	color: #e50914;
	width: 100%;
	margin-top: 30px;
	margin-bottom: 0;
	font-size: 80px;
}
.subTitle {
	padding: 0;
	margin: 0;
	font-size: 26px;
}

@media (max-width: 700px) {
	.form {
		display: flex;
		flex-direction: column;
	}
}

.SignOutBtn {
	position: absolute;
	top: 0;
	right: 0;
	margin-top: 10px;
	margin-right: 10px;
	cursor: pointer;
	padding: 10px 15px;
	background-color: transparent;
	color: white;
	border: none;
	font-size: 14px;
	border: 1px solid white;
	border-radius: 25px;
	text-transform: uppercase;
}

.dashTitle {
	width: 100%;
	text-align: center;
}

.userEmail {
	color: red;
}
.instructions {
	width: 100%;
	text-align: center;
}

@media (max-height: 734px) {
	.SignOutBtn {
		font-size: 9px;
	}
}

.table img {
	width: 80%;
	margin: 0 9px;
}
.table img:hover {
	-webkit-filter: brightness(60%);
	        filter: brightness(60%);
	cursor: pointer;
}

.table {
	display: flex;
	width: 80%;
	flex-direction: column;
	margin: auto;
}
.row {
	width: 100%;
	text-align: center;
	margin: 9px auto;
	display: flex;
}

.imgholder {
	text-align: center;
	display: flex;
	flex-direction: column;
}

